.projects-container {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.projects-container h1 {
  text-align: center;
  color: #00b894;
  margin-bottom: 3rem;
  font-size: 2.5rem;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.project-card {
  background: var(--bg-white);
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
  transition: all 0.3s ease;
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 1.5rem;
}

.project-card h3 {
  color: #2d3436;
  margin-bottom: 0.5rem;
}

.project-card p {
  color: #636e72;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin: 1rem 0;
}

.tech-tag {
  background: #f0f0f0;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.project-links {
  margin-top: 1.5rem;
  text-align: center;
}

.github-link {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  background-color: #00b894;
  color: white !important;
  text-decoration: none;
  border-radius: 25px;
  transition: all 0.3s ease;
  border: none;
}

.github-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 184, 148, 0.2);
  background-color: #00a382;
  color: white !important;
}

.project-indicators {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 2rem;
}

.indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ddd;
  cursor: pointer;
  transition: all 0.3s ease;
}

.indicator.active {
  background-color: #00b894;
  transform: scale(1.2);
}

@media (max-width: 768px) {
  .project-carousel {
    gap: 1rem;
  }

  .project-card {
    padding: 1.5rem;
  }

  .project-card img {
    height: 180px;
  }
}

.project-content {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 1.5rem 0;
  min-height: 180px;
}

.project-content p {
  flex: 1;
  padding: 0 1rem;
  margin: 0;
  line-height: 1.6;
  color: var(--text-secondary);
}

.project-image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.project-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.nav-button {
  background: none;
  border: none;
  color: #00b894;
  cursor: pointer;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease;
}

.nav-button:hover {
  transform: scale(1.1);
}

.content-view,
.achievements-list {
  display: none;
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.project-date {
  font-size: 0.9rem;
  color: #00b894;
  padding: 0.3rem 0.8rem;
  background-color: rgba(0, 184, 148, 0.1);
  border-radius: 15px;
}

.project-card h4 {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin: 0;
}

.project-description-list,
.project-details-list {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin-bottom: 1rem;
  color: var(--text-secondary);
}

.project-description-list li,
.project-details-list li {
  margin-bottom: 0.5rem;
  line-height: 1.6;
}