.education-section {
  padding: 2rem 0;
  background-color: var(--bg-secondary);
}

.education-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.education-section h3 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 2rem;
  text-align: center;
}

.education-cards {
  max-width: 1000px;
  margin: 0 auto;
}

.education-card {
  background: var(--bg-white);
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  margin-bottom: 2rem;
  transition: transform 0.3s ease;
}

.education-card:hover {
  transform: translateY(-5px);
}

.education-card h3 {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  text-align: left;
}

.education-card .university {
  font-size: 1.2rem;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
}

.education-card .period {
  font-size: 1rem;
  color: var(--text-tertiary);
  margin-bottom: 1rem;
}

.education-details {
  margin-top: 1rem;
}

.education-details ul {
  list-style-type: disc;
  padding-left: 1.5rem;
  color: var(--text-secondary);
}

.education-details li {
  margin-bottom: 0.5rem;
  line-height: 1.6;
}

.achievements {
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-color);
}

@media (max-width: 768px) {
  .education-section h4 {
    text-align: center;
    color: #00b894;
    margin-bottom: 2rem;
    font-size: 2rem;
  }
  
  .education-card {
    padding: 1.5rem;
  }
  
  .education-card h3 {
    font-size: 1.25rem;
  }
} 